module.exports = "import React from \"react\";\nimport { TabList } from \"cpstrap\";\n\nlet data = [\n  \"劇情\",\n  \"古裝/史詩\",\n  \"犯罪\",\n  \"動作/冒險\",\n  \"英國歷史\",\n  \"社會議題\",\n  \"男性情誼\",\n  \"名著改編\",\n  \"英式古裝\",\n  \"中古世紀\",\n  \"復仇電影\",\n  \"搶劫犯罪\",\n  \"劫富濟貧\",\n  \"犯罪劇情\",\n  \"古裝動作\",\n  \"復仇動作\",\n  \"浪漫冒險\"\n];\n\nconst _onClick = item => {\n  alert(item)\n};\n\nconst Example = props => {\n  return (\n    <div className=\"row\">\n      <div className=\"col-lg-12\">\n        <TabList>\n          {data.map((item, index)=>{\n            return (\n              <div className=\"item\" key={index} onClick={() => _onClick(item)}>\n                {item}\n              </div>\n            );\n          })}\n        </TabList>\n      </div>\n    </div>\n  );\n};\n\nexport default Example;\n"