module.exports = "import React from 'react';\nimport { Button } from 'cpstrap';\n\nconst Example = (props) => {\n  return (\n    <div>\n      <div>\n        <Button\n          className=\"col-md-12 col-lg-4 mb-10\"\n          onClick={() => alert(\"onClick\")}\n        >\n          即將上架 5月28日\n        </Button>\n        <Button\n          className=\"col-md-12 col-lg-4 mb-10\"\n          color=\"orange-solid\"\n          onClick={() => alert(\"onClick\")}\n        >\n          加入馬上看\n        </Button>\n        <Button\n          className=\"col-md-12 col-lg-4 mb-10\"\n          color=\"orange-solid\"\n          onClick={() => alert(\"onClick\")}\n        >\n          方案介紹\n        </Button>\n      </div>\n      <div\n        style={{\n          marginBottom: \"20px\"\n        }}\n      >\n        <Button className=\"fluid\" onClick={() => alert(\"onClick\")}>\n          加入馬上看\n        </Button>\n      </div>\n      <div\n        style={{\n          marginBottom: \"20px\"\n        }}\n      >\n        <Button\n          color=\"orange-solid\"\n          className=\"fluid\"\n          onClick={() => alert(\"onClick\")}\n        >\n          加入馬上看\n        </Button>\n      </div>\n    </div>\n  );\n};\n\nexport default Example;\n"