module.exports = "import React from 'react';\nimport { Button, ButtonGroup } from 'cpstrap';\nconst tabs = ['Left', 'Right'];\nconst Example = (props) => {\n  return (\n    <ButtonGroup>\n      {tabs.map((item, index) => {\n        return <Button key={index}>{item}</Button>;\n      })}\n    </ButtonGroup>\n  );\n};\n\nexport default Example;\n"